import baseUrl from '../../common/config.js';
import { mapMutations } from 'vuex';
import { mapActions } from 'vuex';
import { mapState } from 'vuex';
export default {
	data() {
		return {
		
		}
	},
	computed:{
		...mapState(['hasLogin','userInfo'])
	},
	methods: {
		...mapMutations(['REMOVE_DATA']),
		...mapActions(['logout']),
		navTo(url){
			uni.showToast({
				title: `跳转到${url}`,
				icon: 'none'
			})
		},
		switchChange(e){
			let switchState = e.detail.value? '打开':'关闭'
			uni.showToast({
				title: `${switchState}了开关`,
				icon: 'none'
			})
		},
		submitLogout(){
			let _self = this;
			uni.showModal({
				content: '确定要退出登录吗',
				success(e) {
					if(e.confirm){
						_self.toLogout();
					}
				}
			})
		},
		async toLogout(){
			if(this.hasLogin){
				let res = await this.logout(this.userInfo.token);
				 uni.showToast({
					title: res.data.message,
					icon: res.data.code == 2000?'success':'none'
				 })
				setTimeout( () => {
					uni.navigateBack();
				},500)
			}else{
				uni.showToast({
					title: '您还没登录呢',
					icon: 'none'
				})
			}
		}
	}
}
